/* -----Mobile menu----- */

.mobile-menu {
  position: fixed;
  overflow-y: scroll;
  top: 0;
  left: 0;
  z-index: 10;
  padding-top: 30px;
  width: 100vw;
  height: 100vh;
  background-color: rgba(17, 21, 24, 0.9);

  transform: translateX(100%);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mobile-menu__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}

.logo__link {
  color: var(--font-color-light);
}

.logo__text-top {
  @include font-pm(18, 0.08, 700, 1.25);
}

.logo__icon {
  vertical-align: middle;
  fill: var(--accent-color);
}

.logo__text-en {
  text-transform: uppercase;
}

.logo__text-bottom {
  margin-top: 8px;
  text-transform: uppercase;
  @include font-pm(7, 0.08, 800, 1.25);
}

.mobile-menu__btn-close {
  border: none;
  background-color: transparent;

  &:hover,
  &:focus {
    fill: var(--accent-color);
  }
}

// .mobile-menu__btn-close-icon {
//   fill: white;
// }

.mobile-menu__nav {
  margin-bottom: 50px;
}

.mobile-menu__item {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.mobile-menu__link {
  color: var(--font-color-light);

  &:hover,
  &:focus {
    color: var(--accent-color);
  }
}

.mobile-menu__btn-cons {
  margin-left: auto;
  margin-right: auto;
  padding: 14px 15px;
  background-color: transparent;
  border: 2px solid var(--accent-color);

  @include font-pm(12, 0.08, 700, 1.25);
  text-transform: uppercase;
  color: var(--accent-color);

  @include flexCenter;
}
